#home-page-container{box-sizing:border-box;height:100%;width:100%;padding:0 5%}#home-description-container{box-sizing:border-box;height:calc(100% / 4);width:100%;margin:0 auto;padding:10% 10%;background-image:url(/portfolio/static/media/background.e88cac6e.png);display:flex;justify-content:center;align-items:center}#home-aboutme-container{box-sizing:border-box;padding:3% 5%;height:calc(100% / 4.5);width:100%}#home-project-container{box-sizing:border-box;height:calc(100% / 2.2);width:100%;margin:0 auto;padding:5% 15%;background-color:#8d99ae;display:flex;flex-direction:column;align-items:center}#footer-container{box-sizing:border-box;padding:10rem 13rem;height:30vh;display:flex;align-items:center}
#heading-container{display:flex;flex-direction:column;align-items:center}#heading-text{color:#edf2f4;text-align:center}#heading-name{color:#ef233c}
#aboutMe-content-container{height:100%;width:100%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}#aboutMe-heading{margin:1.5rem 0}#aboutMe-picture{height:50%;width:35%}#portfolio-pic{height:100%;width:100%;border-radius:2rem;margin:0}#content-container{box-sizing:border-box;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}#aboutMe-para-container{box-sizing:border-box;padding:1% 10%;height:100%;width:100%;font-size:2rem}#content{line-height:150%;font-size:85%;margin-bottom:2rem}
#technologies-lists-container{box-sizing:border-box;padding:2rem;text-align:center}#tech-heading{margin:2rem;margin-bottom:1rem}#hr-line{margin-bottom:2rem}#tech-text{font-size:2rem;margin-top:1rem}@media only screen and (max-width: 360px){#technologies-lists-container{padding:2rem;margin-bottom:2rem}}
#text-box{box-sizing:border-box;height:100%;width:100%;padding:1rem 2rem;background-color:#edf2f4;display:flex;flex-direction:column;align-items:center}@media screen and (max-width: 480px){#text-box{border-radius:4rem;height:auto;width:100%}}
#projects-heading{color:#fff}#projects-container{box-sizing:border-box;height:95%;width:100%;padding:4%;display:flex;flex-direction:column;align-items:center}#project{box-sizing:border-box;height:40%;width:100%;margin-bottom:5%;display:flex;flex-direction:column;justify-content:center;align-items:center}#image-container{height:65%;width:50%;margin-bottom:3rem}#project-image{height:100%;width:100%;border:4px solid #edf2f4;transition:-webkit-transform .5s ease;transition:transform .5s ease;transition:transform .5s ease, -webkit-transform .5s ease}#project-image:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#project-content-container{box-sizing:border-box;width:50%;display:flex;flex-direction:column;align-items:center}#project-heading{display:inline;margin-bottom:1.5rem}#project-content{font-size:2rem}
#link-component-container{width:100%;display:flex;justify-content:space-around}@media only screen and (max-width: 360px){#link-component-container{height:100%;flex-direction:column}}
#links-container{display:flex;flex-direction:column;align-items:center}#link-text{color:#284b63;margin-bottom:.3em;text-decoration:none}#link-text:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}#link-text:visited{color:#ef233c}@media only screen and (max-width: 360px){#links-container{margin-bottom:2rem}}
